export default {
    
	data() {
		return {
			type:0,
            message:"舌象拍摄不符合标准"
		}
	},
	onLoad(op) {
        this.message = op.msg
	},
    onUnload() {
        if(this.type==0){
            uni.navigateBack({
                delta:1
            })
        }
        
    },
	methods: {
        clickCamera:function(){
            this.type = 1
            uni.navigateBack({
                delta:2
            })
        }
	}
}
